.login {
  width: 100%;
  height: 100%;
  background-image: url("./images/bg.jpg");
  //background-size: contain;
  background-size: 100% 100%;
  .login-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 80px;
    background-color: rgba(21, 20, 13, 0.5);
    img {
      width: 40px;
      height: 40px;
      margin: 0 15px 0 50px;
    }
    h1 {
      font-size: 30px;
      color: white;
    }
  }
  .login-content {
    width: 400px;
    height: 300px;
    background-color: #fff;
    margin: 100px auto;
    padding: 20px 40px;
    h2 {
      font-size: 26px;
      text-align: center;
      font-weight: bold;
      margin-bottom: 20px;
    }
    .login-form {
      max-width: 300px;
      .login-form-forgot {
        float: right;
      }
      .login-form-button {
        margin-top: 10px;
        width: 100%;
      }
      .ant-form-item-control {
        line-height: 30px;
      }
    }

  }
}








